@mixin button($color: green){
  height: 24px;
  min-width: 24px;
  padding: 3px;
  text-align: center;
  position: relative;
  display: inline-block;
  color: rgba(0, 0, 0, .7);

  & i{
    font-size: 18px;
  }
  &:active, &.active{
    color: $color;
  }
  &:hover{
    background-color: #eee;
    cursor: pointer;
  }
}

@mixin clear-list(){
  list-style: none;
  margin: 0;
  padding: 0;
}